.Overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.StatsContainer {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: none;
    background-color: #ffffff;
    border-radius: 8px;
    padding: 20px;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.2);
    width: 600px;
    max-height: 90vh;
    overflow-y: auto;
}

.CloseButton {
    position: absolute;
    top: 10px;
    right: 10px;
    background: transparent;
    border: none;
    font-size: 16px;
    cursor: pointer;
    color: #333;
    margin-bottom: 10px;
}

.NoCorrectAnswers {
    padding-top: 10px;
    padding-bottom: 10px;
}


.AnswerHistoryItem {
    margin-bottom: 10px;
    border: 1px solid #ccc;
    padding: 10px;
    background-color: #fffefd;
    width: 550px;
}

.Question {
    font-weight: bold;
}

.CorrectAnswer {
    padding-top: 10px;
}

.UserAnswer {
    font-style: italic;
}

.IsAnswerCorrect {
    font-weight: bold;
    margin-top: 5px;
}

.Correct {
    color: green;
}

.Incorrect {
    color: red;
}